@import "https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Spartan, sans-serif;
}

:root {
  --secound-text-color: #1a1a1a;
  --text-color: #222;
  --background-color: #e3e6f3;
  --main-color: #088178;
}

html {
  scroll-behavior: smooth;
}

h1 {
  color: var(--text-color);
  font-size: 50px;
  line-height: 64px;
}

h2 {
  color: var(--text-color);
  font-size: 46px;
  line-height: 54px;
}

h4 {
  color: var(--text-color);
  font-size: 20px;
}

h6 {
  font-size: 12px;
  font-weight: 700;
}

p {
  color: #465b52;
  margin: 15px 0 20px;
  font-size: 16px;
}

i {
  font-size: 20px;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  margin: 40px 0;
}

button.normal {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 15px 30px;
  font-size: 14px;
  font-weight: 600;
  transition: all .2s;
}

button.white {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  outline: none;
  padding: 11px 30px;
  font-size: 13px;
  font-weight: 600;
  transition: all .2s;
}

body {
  width: 100%;
}

.hidden {
  display: none;
}

.lg-bag {
  color: var(--main-color);
  position: absolute;
  bottom: 10px;
}

.hidden-cart {
  display: none;
}

#header {
  background: var(--background-color);
  z-index: 999;
  justify-content: space-between;
  align-items: center;
  padding: 20px 80px;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 5px 15px #0000000f;
}

#navbar {
  justify-content: center;
  align-items: center;
  display: flex;
}

#navbar li {
  padding: 0 20px;
  list-style: none;
  position: relative;
}

#navbar li a {
  color: var(--secound-text-color);
  transition: .3 ease;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
}

#header li a:hover, #header li a.active {
  color: var(--main-color);
}

#navbar li a.active:after, #header li a:hover:after {
  content: "";
  background: var(--main-color);
  width: 30%;
  height: 2px;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

.logo {
  width: 90px;
}

#mobile {
  align-items: center;
  display: none;
}

#close {
  display: none;
}

.cart__item {
  background-color: var(--main-color);
  z-index: 1000;
  border-radius: 10px;
  min-height: 50px;
  padding: 10px;
  animation: .5s slideIn;
  position: fixed;
  bottom: 0;
  right: 0;
  box-shadow: 0 0 10px #0003;
}

.cart__text {
  color: #fff;
  font-weight: 600;
}

.added-cart {
  display: none;
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

#hero {
  background-image: url("hero3.91c46849.jpg");
  background-position: right 0 top 25%;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 90vh;
  padding: 0 80px;
  display: flex;
}

#hero h4 {
  padding-bottom: 15px;
}

#hero h1 {
  color: var(--main-color);
}

#hero button {
  color: var(--main-color);
  cursor: pointer;
  background-color: #0000;
  background-image: url("button.52502b1b.png");
  background-repeat: no-repeat;
  border: 0;
  padding: 14px 80px 14px 65px;
  font-size: 15px;
  font-weight: 700;
}

#hero button:hover {
  color: #0a4d49;
}

#hero p {
  color: #000;
  font-weight: 700;
}

#feature {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#feature .fe-box {
  text-align: center;
  border: 1px solid #cce7d0;
  border-radius: 4px;
  width: 180px;
  margin: 15px 0;
  padding: 25px 15px;
  box-shadow: 20px 20px 34px #00000008;
}

#feature .fe-box:hover {
  box-shadow: 10px 10px 54px #463edd4d;
}

#feature .fe-box img {
  width: 100%;
  margin-bottom: 10px;
}

#feature .fe-box h6 {
  color: var(--main-color);
  background-color: #fddde4;
  border-radius: 4px;
  padding: 9px 8px 6px;
  line-height: 1;
  display: inline-block;
}

#feature .fe-box h6:nth-child(2) h6 {
  background-color: #cdebbc;
}

#feature .fe-box h6:nth-child(3) h6 {
  background-color: #d1e8f2;
}

#feature .fe-box h6:nth-child(4) h6 {
  background-color: #cdd4f8;
}

#feature .fe-box h6:nth-child(5) h6 {
  background-color: #f6dbf6;
}

#feature .fe-box h6:nth-child(6) h6 {
  background-color: #fff2e5;
}

#product1 {
  text-align: center;
}

#product1 .pro-container {
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
  display: flex;
}

#product1 .pro {
  cursor: pointer;
  border: 1px solid #cce7d0;
  border-radius: 25px;
  width: 23%;
  min-width: 250px;
  margin: 15px 0;
  padding: 10px 12px;
  transition: all .2s;
  position: relative;
  box-shadow: 20px 20px 30px #00000005;
}

#product1 .pro:hover {
  box-shadow: 20px 20px 30px #0000004d;
}

#product1 .pro img {
  border-radius: 20px;
  width: 100%;
}

#product1 .pro .des {
  text-align: start;
  padding: 10px 0;
}

#product1 .pro .des span {
  color: #606063;
  font-size: 12px;
}

#product1 .pro .des h5 {
  color: #1a1a1a;
  padding-top: 7px;
  font-size: 14px;
}

#product1 .pro .des i {
  color: #f3b519;
  font-size: 12px;
}

#product1 .pro .des h4 {
  color: var(--main-color);
  padding-top: 7px;
  font-size: 15px;
  font-weight: 700;
}

#product1 .pro .bxs-shopping-bags {
  color: var(--main-color);
  background-color: #e8f6ea;
  border: 1px solid #cce7d0;
  border-radius: 50px;
  width: 40px;
  height: 40px;
  font-weight: 500;
  line-height: 40px;
  transition: all .3s;
  position: absolute;
  bottom: 20px;
  right: 10px;
}

#product1 .pro .bxs-shopping-bags:hover {
  background: #abceb1;
}

#banner {
  text-align: center;
  background-image: url("banner11.09cf7e3f.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;
  display: flex;
}

#banner h4 {
  color: #fff;
  font-size: 16px;
}

#banner h2 {
  color: #fff;
  padding: 10px 0;
  font-size: 30px;
}

#banner h2 span {
  color: #ef3636;
}

#banner button:hover {
  background-color: var(--main-color);
}

#sm-banner {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

#sm-banner .banner-box {
  background-image: url("banner4.f0f55fa3.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 47%;
  height: 50vh;
  padding: 30px;
  display: flex;
}

#sm-banner .banner-box2 {
  background-image: url("banner3.1cecab2e.jpg");
}

#sm-banner h4 {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
}

#sm-banner h2 {
  color: #fff;
  font-size: 28px;
  font-weight: 800;
}

#sm-banner span {
  color: #fff;
  padding-bottom: 15px;
  font-size: 14px;
  font-weight: 500;
}

#sm-banner .banner-box:hover button {
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
}

#banner3 {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 80px;
  display: flex;
}

#banner3 .banner-box {
  background-image: url("banner5.53c3b33a.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 30%;
  height: 30vh;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

#banner3 .banner-box2 {
  background-image: url("b4.d38a91c4.jpg");
}

#banner3 .banner-box3 {
  background-image: url("banner7.bb85fcf9.jpg");
}

#banner3 h2 {
  color: var(--text-color);
  font-size: 15px;
  font-weight: 800;
}

#banner3 h3 {
  color: var(--main-color);
  font-size: 15px;
  font-weight: 800;
}

#newsletter {
  background-color: #041e42;
  background-image: url("b14.b4ea5e45.png");
  background-position: 20% 30%;
  background-repeat: no-repeat;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#newsletter h4 {
  color: #fff;
  font-size: 22px;
  font-weight: 700;
}

#newsletter p {
  color: #fff;
  font-size: 14px;
  font-weight: 600;
}

#newsletter p span {
  color: #ffdb27;
}

#newsletter .form {
  width: 40%;
  display: flex;
}

#newsletter input {
  border: 1px solid #0000;
  border-radius: 4px 0 4px 4px;
  outline: none;
  width: 100%;
  height: 3.125rem;
  padding: 0 1.25em;
  font-size: 14px;
}

#newsletter button {
  background: var(--main-color);
  color: #fff;
  white-space: nowrap;
  border-top-left-radius: 0;
}

footer {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

footer col {
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
  display: flex;
}

footer .logo {
  margin-bottom: 30px;
}

footer h4 {
  padding-bottom: 20px;
  font-size: 14px;
}

footer p {
  margin: 0 0 8px;
  font-size: 13px;
}

footer a {
  color: #222;
  margin: 0 0 10px;
  font-size: 13px;
  text-decoration: none;
}

footer .follow {
  margin-top: 20px;
}

footer .follow i {
  color: #465b52;
  cursor: pointer;
  padding-right: 4px;
}

footer .install .row img {
  border: 1px solid var(--main-color);
  border-radius: 6px;
}

footer .install img {
  margin: 10px 0 15px;
}

footer .follow i:hover, footer a:hover {
  color: var(--main-color);
}

footer .copyright {
  text-align: center;
  width: 100%;
}

#page-header {
  text-align: center;
  background-image: url("b1.d9897392.jpg");
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 40vh;
  padding: 14px;
  display: flex;
}

#page-header h2, #page-header p {
  color: #fff;
}

#pagination {
  text-align: center;
}

#pagination a {
  background: var(--main-color);
  color: #fff;
  border-radius: 4px;
  padding: 15px 20px;
  font-weight: 600;
  text-decoration: none;
}

#pagination a .arrow {
  font-size: 16px;
  font-weight: 600;
}

#prodetails {
  margin-top: 20px;
  display: flex;
}

#prodetails .single-pro-image {
  width: 40%;
  margin-right: 50px;
}

.small-img-group {
  justify-content: space-between;
  display: flex;
}

.small-img-col {
  cursor: pointer;
  border: 4px solid var(--main-color);
  flex-basis: 24%;
}

.img-container {
  border-top: 4px solid var(--main-color);
  border-right: 4px solid var(--main-color);
  border-left: 4px solid var(--main-color);
}

#prodetails .single-pro-details {
  width: 50%;
  padding-top: 30px;
}

#prodetails .single-pro-details h4 {
  padding: 40px 0 20px;
}

#prodetails .single-pro-details h2 {
  font-size: 26px;
}

#prodetails .single-pro-details select {
  margin-bottom: 10px;
  padding: 5px 10px;
  display: block;
}

#prodetails .single-pro-details input {
  width: 50px;
  height: 47px;
  margin-right: 10px;
  padding-left: 10px;
  font-size: 16px;
}

#prodetails .single-pro-details button {
  background: var(--main-color);
  color: #fff;
}

#prodetails .single-pro-details input:focus {
  outline: none;
}

#prodetails .single-pro-details span {
  line-height: 25px;
}

#link__1:hover, #link__2:hover, #link__3:hover {
  background-color: #075752;
}

#title {
  text-align: center;
  background-image: url("blog2.d10ad6e1.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;
  display: flex;
}

#title h1 {
  color: #fff;
}

#main {
  justify-content: center;
  display: flex;
}

#main .content {
  justify-content: center;
  align-items: center;
  gap: 5%;
  width: 70%;
  margin: 5%;
  display: flex;
}

#main h4 {
  font-size: 3rem;
}

#main .content__img {
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
  width: 37rem;
  height: 45vh;
}

#main .content__text {
  width: 37rem;
}

#main #img1 {
  background-image: url("b3.63303d11.jpg");
}

#main #img2 {
  background-image: url("blog6.085691e1.jpg");
}

#main #img3 {
  background-image: url("b4.6f7495f9.jpg");
}

#main #img4 {
  background-image: url("online.5794fe19.jpg");
}

#main .content__text p {
  letter-spacing: .7px;
  line-height: 25px;
}

#title-about {
  text-align: center;
  background-image: url("banner.268fd294.png");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;
  display: flex;
}

#title-about h1 {
  color: #fff;
}

#title-about p {
  color: #adadad;
}

#img-about {
  background-image: url("about1.69bb76ae.jpg");
  background-position: center;
}

#app {
  text-align: center;
}

.wrapper__title h3 {
  margin: 3rem;
  font-size: 3.5vw;
  font-weight: bold;
}

.wrapper__video video {
  border-radius: 10%;
  width: 70%;
}

#app__link {
  background: var(--main-color);
  border-radius: 50%;
  padding: 20px;
}

#our {
  color: var(--main-color);
}

#download {
  border-left: 4px solid var(--main-color);
  padding: 10px;
  animation: 1.5s infinite bord;
}

#wrapper .wrapper__button {
  margin-top: 30px;
}

#btn__app {
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  color: #fff;
  border-radius: 15px;
  width: 12rem;
  height: 3rem;
  font-size: 16px;
  font-weight: 700;
}

#btn__app:hover {
  cursor: pointer;
  background-color: #0c504b;
}

@keyframes bord {
  0% {
    border-left: none;
  }

  50% {
    border-left: 4px solid var(--main-color);
  }

  100% {
    border-left: none;
  }
}

#contact-details {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#contact-details .details {
  width: 40%;
}

#contact-details .details span, #form-details form span {
  font-size: 12px;
}

#contact-details .details h2, #form-details form h2 {
  padding: 20px 0;
  font-size: 26px;
  line-height: 35px;
}

#contact-details .details h3 {
  padding-bottom: 15px;
  font-size: 16px;
}

#contact-details .details li {
  padding: 10px 0;
  list-style: none;
  display: flex;
}

#contact-details .details li i {
  padding-right: 22px;
  font-size: 14px;
}

#contact-details .details li p {
  margin: 0;
  font-size: 14px;
}

#contact-details .map {
  width: 55%;
  height: 400px;
}

#form-details {
  border: 1px solid #e1e1e1;
  justify-content: space-between;
  margin: 30px;
  padding: 80px;
  display: flex;
}

#contact-details .map iframe {
  width: 100%;
  height: 100%;
}

#form-details form {
  flex-direction: column;
  align-items: flex-start;
  width: 65%;
  display: flex;
}

#form-details form input, #form-details form textarea {
  border: 1px solid #e1e1e1;
  outline: none;
  width: 100%;
  margin-bottom: 20px;
  padding: 12px 15px;
}

#form-details form button {
  background: var(--main-color);
  color: #fff;
}

#form-details form button:hover {
  background: #075752;
}

#form-details .people div {
  align-items: flex-start;
  padding-bottom: 25px;
  display: flex;
}

#form-details .people div img {
  object-fit: cover;
  width: 65px;
  height: 65px;
  margin-right: 15px;
}

#form-details .people div nav {
  margin: 0;
  font-size: 13px;
  line-height: 25px;
}

#form-details .people div p span {
  color: #000;
  font-size: 16px;
  font-weight: 600;
  display: block;
}

#form-details form span {
  font-size: 2rem;
}

#cart table {
  border-collapse: collapse;
  table-layout: fixed;
  white-space: nowrap;
  width: 100%;
}

#cart table img {
  width: 6rem;
}

#cart tr {
  justify-content: space-around;
  display: flex;
}

#cart table thead {
  border: 1px solid #e2e9e1;
  border-left: none;
  border-right: none;
  transition: all .5s;
}

#cart table thead td {
  text-transform: uppercase;
  padding: 18px 0;
  font-size: 13px;
  font-weight: 700;
}

#cart tbody {
  border: 1px solid #e2e9e1;
}

#cart tbody td {
  padding: 1rem 0;
}

#cart1 table {
  border-collapse: collapse;
  table-layout: fixed;
  white-space: nowrap;
  width: 100%;
}

#cart1 table img {
  width: 6rem;
}

#cart1 tr {
  justify-content: space-around;
  display: flex;
}

#cart1 tr input {
  width: 30px;
  height: 30px;
}

#cart1 table thead {
  border: 1px solid #e2e9e1;
  border-left: none;
  border-right: none;
}

#cart1 table thead td {
  text-transform: uppercase;
  padding: 18px 0;
  font-size: 13px;
  font-weight: 700;
}

#cart1 tbody {
  border: 1px solid #e2e9e1;
  display: flex;
}

#cart1 tbody td {
  padding: 1rem 0;
}

.delete-button {
  background: none;
  border: none;
}

.delete-button:hover {
  cursor: pointer;
}

.delete-button .bx-x {
  width: 20px;
}

#total-price {
  color: var(--secound-text-color);
  text-align: center;
  margin: 3rem;
}

#total-price span {
  color: var(--main-color);
}

#wrapper .wrapper__button {
  margin-top: 30px;
}

.pay {
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  color: #fff;
  border-radius: 15px;
  width: 5rem;
  height: 2.5rem;
  margin-top: 1rem;
  font-size: 16px;
  font-weight: 700;
}

.pay:hover {
  cursor: pointer;
  background-color: #0c504b;
}

@media (width <= 1222px) {
  #main .content__img {
    height: 30vw;
  }

  .wrapper {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-top: 5rem;
    display: flex;
  }

  .wrapper .details {
    width: 50%;
    margin-left: 5rem;
  }

  .wrapper .map {
    width: 50%;
    margin-right: 5rem;
  }
}

@media (width <= 1000px) {
  #main .content {
    text-align: center;
    display: block;
  }

  #main .content__img {
    border-radius: 0%;
    width: 100%;
    height: 22rem;
  }

  #main .content__text {
    width: 100%;
  }

  #main .content__text h4 {
    margin: 50px 0;
  }
}

@media (width <= 799px) {
  .section-p1 {
    padding: 40px;
  }

  #hero__text124 {
    color: #fff;
  }

  #navbar {
    background-color: #e3e6f3;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 300px;
    height: 100vh;
    padding: 80px 0 0 10px;
    transition: all .6s;
    display: flex;
    position: fixed;
    top: 0;
    right: -300px;
    box-shadow: 0 40px 60px #0000001a;
